@import "../variables.scss";

header {
  position: sticky;
  @include breakpoint($phone) {
    position: static;
  }
  top: 0;
  z-index: 999;
  display: flex;
  flex-direction: column;
  .header-wrap {
    width: 100%;
    padding: 18px 8px;
    background: $secondary;
    color: white;
    display: flex;
    justify-content: space-between;
    a {
      color: white;
      text-decoration: none;
    }
    .block {
      display: flex;
      align-items: center;
      justify-content: space-evenly;
      width: 32%;
      flex-wrap: wrap;
      font-size: 14px;
      font-weight: 300;
    }
    .wrap {
      display: flex;
      flex-direction: column;
      justify-content: center;
      a {
        text-align: center;
      }
      .row {
        margin-bottom: 2px;
        white-space: nowrap;
      }
    }
    .left {
      img {
        max-width: 100px;
        max-height: 40px;
      }
    }
    .center {
      font-family: $lato;
      font-weight: 900;
      font-size: 36px;
    }
    .right {
      // padding-left: 32px;
      // display: flex;
      // justify-content: flex-end;
    }
  }
  nav {
    background: white;
    border-bottom: 1px solid #e0e0e0;
    width: 100%;
    overflow-x: scroll;
    padding: 1px 0;
    ul {
      display: flex;
      width: 100%;
      list-style-type: none;
      padding: 0 10px;
      li {
        background: $primary;
        color: white;
        padding: 12px 18px;
        border-radius: 30px;
        margin: 0 6px;
        letter-spacing: .05em;
        white-space: nowrap;
        opacity: .5;
        &.active {
          opacity: 1;
        }
      }
    }
  }
}
