// Пути

$fonts: "../fonts";
$img: "../img";

// color-theme

$color-black: #000000;
$color-white: #ffffff;
$primary: #C2141C;
$secondary: #232754;
$special-normal: #7668F6;

$gray-main: #777;
$gray-disable: #f5f5f5;
$gray-disble-text: #c1c1c1;

$status-green: #27AE60;
$status-orange: #F2994A;
$status-red: #EB5757;
$status-blue: #2D9CDB;
$status-brown: #B48C45;
$status-yellow: #E3B100;

$g-orange: linear-gradient(to right, #E88D17, #FF6663);
$g-blue: linear-gradient(to right, #17A6E8, #17A6E8);

$tablet: min-width 900px;
$phone: max-width 400px;

$padding-m: 0 16px;
$letter-space: .1rem;
$margin-b: 14px;

$headerH: 87px;
$bottomH: 265px;
$btnH: 70px;

$sectionH: calc(100% - 517px);
$indexH: calc(100% - 60px);

$lato: 'Lato', sans-serif;

.stuff {
  background: $gray-disable;
}

.max-body {
  max-width: 900px;
  margin: auto !important;
  overflow-x: hidden;
}

.m-auto {
  margin: auto;
  padding: $padding-m;
}
