@import "../variables.scss";
$btn-font-size: 16px;

.full-btn {
  width: 100%;
  margin: auto;
  position: sticky;
  bottom: 0;
  background: $primary;
  color: white;
  z-index: 98;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  letter-spacing: .15em;
  font-size: $btn-font-size;
  padding: 21px 0;
  z-index: 99;
  text-decoration: none;
  &.disabled {
    background-color: $gray-disable;
    color: $gray-disble-text !important;
    text-decoration: none !important;
    font-weight: 400;
  }
}
.white-btn {
  background: white;
  color: $primary;
  text-decoration: underline;
  z-index: 99;
}
.add-to-cart-btn {
  width: 100%;
  margin: auto;
  position: sticky;
  position: fixed;
  bottom: 0;
  background: white;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 16px 16px 16px;
  letter-spacing: .15em;
  font-size: $btn-font-size;
  z-index: 99;
  .btn {
    background-color: $primary;
    color: white;
    z-index: 99;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 24px 0;
    position: relative;
    span.cart-price {
      position: absolute;
      right: 3%;
      letter-spacing: .05em;
    }
  }
  &.disabled {
    .btn {
      background-color: $gray-disable;
      color: $gray-disble-text;
    }
  }
  &a {
    text-decoration: none;
  }
}

.static {
  position: static;
}
.outline {
  text-decoration: none;
  border: 1px solid $primary;
  background: white;
  color: $primary;
}

.date-wrap {
  &:first-child {
    margin-bottom: 2rem;
  }
}
.swal-footer {
  text-align: center !important;
}
