@import "../variables.scss";

.index-body {
  height: 100%;
  background-size: cover;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  text-align: center;
  color: white;
  padding: 5rem 0;
  .bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0.4) 76.86%, #000000 100%);
  }
  .logo-wrap, .index-center, .index-bottom {
    width: 95%;
    z-index: 1;
    input {
      width: 80%;
      outline: none;
      border: 1px solid $secondary;
      margin-bottom: 1.5rem;
      background: rgba($color: #fff, $alpha: .7);
      border: 3px solid rgba($color: $secondary, $alpha: 0);
      padding: 1.5rem;
      font-size: 24px;
      font-family: $lato;
      letter-spacing: .05em;
      transition: all 300ms;
      @include breakpoint($phone) {
        font-size: 16px;
        width: 95%;
        padding: 1.2rem;
      }
    }
    input:focus {
      background: rgba($color: #fff, $alpha: .85);
      border: 3px solid rgba($color: $secondary, $alpha: .4);
    }
  }
  .login {
    margin-bottom: auto;
    margin-top: 5rem;
    .full-btn {
      @include breakpoint($phone) {
        font-size: 18px !important;
        width: 95% !important;
      }
    }
  }
  .logo-wrap {
    img {
      max-width: 80%;
      max-height: 250px;
    }
    font-size: 20px;
    line-height: 1rem;
    letter-spacing: .05em;
    .row {
      margin-bottom: 1rem;
      @include breakpoint($phone) {
        font-size: 14px;
        margin-bottom: .5rem;
      }
    }
  }
  .index-center {
    h1 {
      font-size: 80px;
      margin-top: -1em;
      font-family: $lato;
    }
    h2 {
      font-weight: 500;
      font-size: 40px;
    }
    a {
      color: white;
      text-decoration: underline;
      font-weight: 400px;
      font-size: 24px;
      margin-top: .5rem;
      display: block;
    }
    .full-btn {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 80%;
      font-weight: bold;
      font-size: 30px;
      padding: 2rem 0;
      margin-bottom: 0.67em;
      font-family: $lato;
    }
    @include breakpoint($phone) {
      h1 {
        font-size: 40px;
      }
      h2 {
        font-size: 18px;
      }
      .full-btn {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 80%;
        font-weight: bold;
        font-size: 16px;
        padding: 1.2rem 0;
        margin-bottom: 0.67em;
        font-family: $lato;
        text-decoration: none;
      }
      a {
        font-size: 16px;
        margin-top: .5rem;
        display: block;
      }
    }
  }
  .index-bottom {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80%;
    text-align: center;
    font-size: 18px;
    line-height: 3rem;
    letter-spacing: .05em;
    .row {
      width: 80%;
      display: flex;
      justify-content: space-between;
    }
    .full-btn {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 80%;
      font-weight: bold;
      font-size: 30px;
      padding: 1.5rem 0;
      margin-bottom: 0.67em;
      font-family: $lato;
    }
    @include breakpoint($phone) {
      .full-btn {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 80%;
        font-weight: bold;
        padding: .5rem 0;
        margin-bottom: 0.67em;
        font-family: $lato;
      } 
    }
  }
}
