@import "../variables.scss";

.detail-info {
  margin: 1.4rem 0 .8rem 0;
  padding: 16px !important;
  h3.title {
    font-size: 26px;
    font-weight: 500;
    padding-bottom: 16px;
  }
  .p {
    font-size: 15px;
  }
}
.sub-detail-info {
  h3.title {
    font-size: 26px;
    font-weight: 500;
    padding-bottom: 16px !important;
  }
}
.sub-detail-info, .cart {
  h3.title {
    font-size: 26px;
    font-weight: 500;
    padding-top: 1.5rem;
    margin-bottom: 0;
    margin-top: 0;
    @include breakpoint($phone) {
      padding-top: 1rem;
    }
  }
}
.list {
  min-height: $sectionH;
  overflow-y: scroll;
  margin-bottom: 90px;
}
.cart {
  min-height: $sectionH;
  overflow-y: scroll;
  p.title {
    color: $gray-main;
    font-weight: 300;
    width: 100%;
    text-align: center;
    line-height: 1.5rem;
    letter-spacing: .05em;
    margin-bottom: 0;
  }
  .input-wrap {
    margin: 0 !important;
    .number {
      background-color: $primary;
      font-size: 10px;
      letter-spacing: 0;
      text-align: center;
      color: white;
      padding: 4px;
      display: flex;
      justify-content: center;
      align-items: center;
      white-space: nowrap;
      z-index: 1;
      width: 21px;
      height: 21px;
      margin-right: 1rem;
      line-height: 0;
    }
    .price {
      margin-top: 0;
      padding-bottom: 0;
      align-self: center;
      margin-left: auto;
      flex-direction: row-reverse;
      white-space: nowrap;
      .discount {
        margin-left: 4px;
      }
    }
    &.cancel {
      opacity: 50%;
      .number {
        background: $gray-main;
      }
      .price {
        flex-wrap: wrap;
        .discount {
          color: $gray-main;
        }
        &::after {
          content: "(已取消)";
          display: block;
          width: 100%;
          text-align: right;
        }
      }
    }
  }
}
.cart-bottom-wrap {
  position: absolute;
  bottom: 0;
  width: 100%;
  a, div, .fieldset {
    position: static !important;
    bottom: auto;
  }
}
.detail-cover {
  width: 100%;
  height: 350px;
  background: no-repeat;
  background-size: cover;
  background-position: center;
  img {
    object-fit: cover;
  }
}
fieldset, .fieldset {
  border: none;
  display: flex;
  flex-direction: column;
  padding: 0;
  &.note {
    position: sticky;
    bottom: 80px;
  }
  legend, .note {
    background: $gray-disable;
    width: 100%;
    padding: 0;
    margin: 0;
    padding-inline-start: 16px;
    padding-inline-end: 16px;
  }
  legend {
    font-size: 21px;
    font-weight: 400;
    letter-spacing: .05em;
    line-height: 31px;
    padding: 12px 16px;
  }
  .note {
    font-size: 16px;
    line-height: 24px;
    letter-spacing: .05em;
    color: $gray-main;
    padding-bottom: 12px;
    margin-top: -11px;
  }
  .input-wrap {
    display: flex;
    align-items: baseline;
    justify-content: flex-start;
    padding: 16px 0;
    margin: 0 16px;
    line-height: 24px;
    position: relative;
    border-bottom: 1px solid #e0e0e0;
    &:focus {
      outline: none;
    }
    &:last-child {
      margin-bottom: 20px;
    }
    .label-wrap {
      display: flex;
      flex-direction: column;
      margin-right: .8rem;
      .p {
        margin-top: -8px;
      }
    }
    img {
      margin-left: auto;
    }
    label {
      display: flex;
      align-items: center;
      width: 100%;
      font-size: 16px;
      letter-spacing: .1em;
      padding: 12px 0;
      .price {
        padding-bottom: 0;
        margin-left: auto;
        flex-direction: row-reverse;
        font-size: 16px;
        .new-price {
          margin-right: .5rem;
        }
      }
    }
    input {
      margin-right: 14px;
      transform: scale(1.6);
      opacity: .6;
    }
  }
  textarea {
    outline: none;
    border: none;
    border-bottom: 1px solid #e0e0e0 !important;
    border-top: 1px solid $gray-disable !important;
    min-height: 90px;
    resize: none;
    padding: 20px 16px;
    font-size: 16px;
    color: black !important;
    font-family: 'Noto Sans TC', Arial, Helvetica, sans-serif;
    letter-spacing: .1em;
    &:focus, &:not(.js-focus-visible) {
      border: 0;
      outline-width: 0 !important;
    }
    &::placeholder {
      color: $gray-disble-text;
    }
  }
}

input, label {
  outline-width: 0;
  &:focus {
    outline: none;
    outline-width: 0 !important;
    opacity: 1;
  }
  &:target {
    outline: none;
    outline-width: 0;
    opacity: 1;
  }
  &:not(.js-focus-visible) {
    outline: none;
    outline-width: 0;
    opacity: 1;
  }
}

input:disabled + label {
  color: $gray-disble-text;
}

.quality-wrap {
  display: flex;
  justify-content: center;
  margin: 60px 0;
  $btn-size: 80px;
  input {
    text-align: center;
    outline: none;
    border: 0;
    width: $btn-size;
    margin: 0 1.5rem;
    font-size: 18px;
    font-family: $lato;
  }
  .minus, .plus {
    background: $secondary;
    width: $btn-size;
    height: $btn-size;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 30px;
    line-height: 0;
    cursor: pointer;
    &.disabled {
      background: $gray-disable;
      color: $gray-disble-text;
      &.stuff-m {
        background: $secondary !important;
      }
    }
  }
}
