@import "../variables.scss";

.table-detail {
  width: 95%;
  max-width: 460px;
  letter-spacing: .1em;
  padding: 1rem 0 2rem 0;
  .table-detail-title-wrap {
    text-align: center;
    .table-name {
      font-family: $lato;
      font-weight: 900;
      font-size: 68px;
      color: $primary;
    }
  }
  input.flatpickr {
    padding: 1rem;
    border: 1px solid $secondary;
    font-family: $lato;
    position: relative;
    width: 100%;
    background: white;
    font-size: 16px;
    letter-spacing: .05em;
  }
  input[type="time" i] {

  }
  .flatpickr-input[readonly] {
    cursor: pointer;
  }
  .quality-wrap {
    margin: 0px 0;
  }
  .minus {
    &.disabled {
      background: white !important;
    }
  }
  h3.title {
    padding: 1rem 0;
    margin-bottom: 0;
  }
  .full-btn {
    margin-top: 4rem !important;
    padding: 16px 0 !important;
    &.outline {
      margin-top: 2rem !important;
    }
  }
  .table-detail-form {
    h3.title {
      padding: 1.5rem 0;
      margin-bottom: 0;
      @include breakpoint($phone) {
        padding-top: 1rem;
      }      
    }
  }
}
.stuff {
  .center {
    font-size: 18px !important;
    letter-spacing: .1em;
  }
  .quality-btn {
    .disabled {
      background: $secondary !important;
    }
  }
}
.table-status {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  li {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin: 1rem 1rem 0rem 0;
  font-weight: 400px;
  color: #555;
  letter-spacing: .1em;
    &::before {
      content: '';
      width: 14px;
      height: 14px;
      display: block;
      margin-right: 4px;
      border-radius: 50%;
    }
    &.empty {
      &::before {
        border: 2px solid $status-brown;
        background: white;
      }
    }
    &.no-order {
      &::before {
        background: $special-normal;
      }    
    }
    &.ordered {
      &::before {
        background: $status-green;
      }  
    }
    &.ready-togo {
      &::before {
        background: $status-red;
      }      
    }
    &.paid {
      &::before {
        background: $status-orange;
      }    
    }
    &.reserve {
      &::before {
        background: $status-blue;
      }
    }
  }
}
.all-tables {
  display: flex;
  flex-direction: column;
  .table-wrap {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    .table {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      width: 20%;
      // max-width: 200px;
      min-width: 160px;
      max-height: 200px;
      margin: 1.5rem 1rem;
      font-family: $lato;
      color: white;
      font-size: 14px;
      letter-spacing: .1em;
      @include breakpoint($phone) {
        margin: 1.5rem .5rem;
      }
      &.timesup {
        position: relative;
        &::after {
          content: url('../../../img/Icon/Nav/Icon/timesup.svg');
          display: flex;
          justify-content: center;
          align-items: center;
          border-radius: 50%;
          border: 4px solid white;
          position: absolute;
          right: -20px;
          top: -24px;
          padding: 9px 13px;
        }
      }
      .member {
        background: rgba($color: #ffffff, $alpha: .25)
        
      }
      .table-number {
        background-blend-mode: multiply;
        color: white;
        padding: 8px 10px;
        border-radius: 18px;
        position: relative;
        border: 2px solid white;
      }
      &.empty {
        background: white;
        border: 1px solid $status-brown;
        .member {
          background: $status-brown;
          color: white;
        }
        .table-number {
          color: $status-brown;
          border: 1.5px solid $status-brown;
        }
        span {
          color: $status-brown;
        }
        &.active {
          outline: 10px solid rgba($color: $status-brown, $alpha: .25);
        }         
      }
      .member {
        padding: 6px 0;
        border-radius: 16px;
      }
      &.no-order {
        background: rgba($color: $special-normal, $alpha: 1);
        .table-number {
          background: $special-normal;
        }
        &.active {
          outline: 10px solid rgba($color: $special-normal, $alpha: .25);
        }          
      }
      &.ordered {
        background: rgba($color: $status-green, $alpha: 1);
        .table-number {
          background: $status-green;
        }
        &.active {
          outline: 10px solid rgba($color: $status-green, $alpha: .25);
        }         
      }
      &.ready-togo {
        background: rgba($color: $status-red, $alpha: 1);
        .table-number {
          background: $status-red;
        }
        &.timesup {
          &::after {
            background: $status-red;
          }
        }
        &.active {
          outline: 10px solid rgba($color: $status-red, $alpha: .25);
        }          
      }
      &.paid {
        background: rgba($color: $status-orange, $alpha: 1);
        .table-number {
          background: $status-orange;
        }
        &.timesup {
          &::after {
            background: $status-orange;
          }
        }  
        &.active {
          outline: 10px solid rgba($color: $status-orange, $alpha: .25);
        }         
      }
      &.reserve {
        background: rgba($color: $status-blue, $alpha: 1);
        .table-number {
          background: $status-blue;
        }
        &.timesup {
          &::after {
            background: $status-blue;
          }
        }
        &.active {
          outline: 10px solid rgba($color: $status-blue, $alpha: .25);
        }      
      }
      
      .row {
        margin: 15px 0;
        display: flex;
        justify-content: center;
        width: 80%;
        img {
          transform: scale(1.2);
          margin-right: .2rem;
        }
      }
      .row:first-child {
        font-size: 15px;
        justify-content: space-between;    
        align-items: center;
        img {
          max-height: 13px;
        }
        span {
          display: block;
          margin-right: .3rem;
        }
      }
    }
  }
}
.edit-wrap {
  display: flex;
  width: 100%;
  .input-wrap {
    width: 100%;
  }
  .action-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    div {
      margin-left: 1rem;
      min-width: 20px;
      min-height: 20px;
      display: block;
    }
    div:first-child {
      margin-left: 2rem;
    }
  }
}
.stuff-edit {
  margin-right: 90px;
}
