@import "../variables.scss";

.menu-block-list {
  display: flex;
  flex-direction: column;
  padding: 0 16px;
  margin-bottom: $btnH;
  @include breakpoint($tablet) {

  }
  .menu-block-wrapper {
    width: 100%;
    @include breakpoint($tablet) {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
    }
  }
}
.menu-block {
  display: flex;
  position: relative;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 41px;
  @include breakpoint($tablet) {
    width: 49%;
    border: 1px solid #e0e0e0;
    padding: 10px;
    display: flex;
  }
  &::after {
    content: "";
    width: 100%;
    height: .5px;
    background-color: #e0e0e0;
    display: block;
    position: absolute;
    bottom: -21px;
    @include breakpoint($tablet) {
      display: none;
    }
  }
  &.disabled {
    opacity: 50%;
    .price {
      color: $gray-main !important;
    }
    img {
      margin-right: 4px;
    }
  }
  .title, .p {
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;  
    overflow: hidden;
  }
  .left {
    display: flex;
    flex-direction: column;
  }
  .right {
    position: relative;
    margin-left: 1.2rem;
    .img {
      width: 100px;
      height: 100px;
      margin-left: auto;
      img {
        object-fit: cover;
      }
    }
    .number {
      background-color: $primary;
      font-size: 10px;
      letter-spacing: 0;
      text-align: center;
      color: white;
      padding: 4px;
      display: flex;
      justify-content: center;
      align-items: center;
      white-space: nowrap;
      position: absolute;
      right: 0;
      top: 0;
      z-index: 1;
      width: 21px;
      height: 21px;
      margin-left: 1rem;
      line-height: 0;
    }
  }
}
h2.label {
  font-size: 26px;
  font-weight: 500;
  // padding-top: 21px;
  letter-spacing: $letter-space;
  line-height: 30px;
  text-align: left;
  width: 100%;
}
h3.title {
  font-size: 20px;
  font-weight: 400;
  line-height: 30px;
  margin-top: 0;
  letter-spacing: $letter-space;
  margin-bottom: $margin-b;
  @include breakpoint($phone) {
    font-size: 20px !important;
  }
}
h3.red {
  color: $primary;
}
h3.center {
  text-align: center;
}
.p {
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  letter-spacing: $letter-space;
  margin-bottom: $margin-b;
  color: $gray-main;
}
.badge-wrap {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  .badge {
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: $letter-space;
    display: flex;
    margin-right: .5rem;
    color: black;
    margin-bottom: $margin-b;
    display: flex;
    align-items: center;
    width: fit-content;
    img {
      min-width: 20px;
      min-height: 20px;
      margin-right: 3px;
    }
    &.good {
      background-color: $primary;
      color: white;
      padding: 3px 10px;
      border-radius: 50px;
      img {
        min-width: 16px;
        min-height: 16px;
      }
    }
    &.check {
  
    }
    &.warning {
      color: $status-yellow;
    }
  }
}


.price {
  font-size: 15px;
  letter-spacing: .05em;
  display: flex;
  margin-top: auto;
  padding-bottom: .8rem;
  .discount {
    text-decoration: line-through;
  }
  .new-price {
    color: $special-normal;
    margin-left: 4px;
  }
  img {
    min-width: 20px;
    min-height: 20px;
    margin-right: 3px;
  }
  &.sub-total {
    justify-content: flex-end;
    padding-top: 16px;
    span {
      margin-left: .5rem;
    }
  }
}
